.container {
  margin: 0px auto;
  max-width: 1000px; }

h1, h2, h3, h4, h5, h6 {
  font-weight: 400;
  margin: 0; }

h1 {
  margin: 80px 0;
  font-size: 43px;
  line-height: 1em; }
  h1 a {
    color: #394041;
    text-decoration: none; }
    h1 a:hover {
      border-radius: 2px;
      background: #ffffff;
      box-shadow: 20px 20px 60px #d9d9d9, -20px -20px 60px #ffffff; }

h2 {
  margin: 0 0 30px 0;
  font-size: 16px;
  line-height: 1em;
  font-style: italic; }

h3 {
  margin: 12px 0;
  font-size: 30px;
  line-height: 1em; }

p {
  margin: 12px 0;
  font-size: 12px;
  line-height: 1.4em;
  font-style: italic; }

a {
  color: #008BFF;
  text-decoration: none; }

.cards {
  display: flex;
  flex-wrap: wrap;
  margin: 0 -40px; }
  .cards .card {
    padding: 20px 40px 20px;
    box-sizing: border-box;
    width: calc(100%/3);
    transition: box-shadow .25s ease-out; }
    .cards .card:hover {
      border-radius: 2px;
      background: #ffffff;
      box-shadow: 20px 20px 60px #d9d9d9, -20px -20px 60px #ffffff; }

.tags {
  margin: 12px 0;
